@import '../../theme/variables';

.byline-text {
  font-size: em(13px);
  margin-top: -0.75em;
  margin-bottom: 2rem;
  a {
    color: black;
    &:active,
    &:hover,
    &:visited,
    &:focus {
      color: black;
    }
  }
}

.byline-text__author{
  text-transform: uppercase;
}
.byline-text__datetime{
  text-transform: uppercase;
  padding: 0 .5em;
}



