@import '../../theme/variables';
.browse-conditions {
  font-size: 1rem;
}
.browse-conditions__well {
  padding: 0.4em 2.5em;
  margin: 0;
  ul li a {
    &:hover {
      background-color: $azure;
      text-decoration: none;
      color: white;
    }
  }
}
.browse-conditions__header {
  font-family: $font-family-sans-serif;
  margin: 0 auto;
  float: none;
  h1 {
    font-family: $font-family-serif;
  }
}
.browse-conditions__search-by-letter {
  padding-top: 2.2em;
}



