.text-details {
  font-size: 1rem;
  display: block;
  ol,
  ul {
    list-style: none;
    padding-left: 0;
    & + & {
      margin-top: .5em;
    }
    &:last-child{
      padding-bottom: 0;
    }
  }
  hr {
    margin: 10px 0;
  }
  .label-caption {
    font-weight: bold;
  }
  p + p{
    margin-top: 0.5em;
  }
  .label-value__group {
    display: block;
  }
  .label-value__group + .label-value__group {
    margin-top: .5em;
  }
}
.label-view-all {
  font-weight: bold;
}
.space-between-items {
  & ul > li {
    padding: 5px 0;
    &:last-child{
      padding-bottom: 0;
    }
  }
}
.fa {
  display: inline-block;
  line-height: inherit;
  padding: 0 .5em;
}



