/*

 Sass Functions
 */
.result-card {
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  padding: 0;
  flex-basis: 100%;
  max-width: 100%;
  border: 0;
}

@media (min-width: 768px) {
  .result-card {
    margin-bottom: 2.5em;
  }
}

@media (max-width: 768px) {
  .result-card {
    margin-bottom: 1.25em;
  }
}

.result-card > [class*="row-"],
.result-card > [class*=" row-"] {
  margin-left: -0.5em;
  margin-right: -0.5em;
}

.result-card .card-column--text {
  max-height: 140px;
}

.result-card .card-column--figure {
  flex-basis: calc(213px + 1em);
  max-width: calc(213px + 1em);
}

.result-card__figure {
  position: relative;
}

.result-card__category-label {
  font-size: 0.750em;
  text-transform: uppercase;
  margin-top: -1em;
  display: inline-block;
}

.result-card__category-label:after {
  content: '';
  clear: both;
  display: block;
}

.result-card__byline {
  font-size: 0.750em;
  margin-bottom: 0.5em;
  margin-top: 0.25em;
}

.result-card__figure-overlay {
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1em;
  margin: -0.5em;
  align-items: flex-start;
  justify-content: flex-end;
}

.result-card__figure__icon {
  margin-top: 0em;
  font-size: 2em;
  vertical-align: text-top;
  line-height: 1.4;
  padding: 0 0.25em;
}

.result-card__figure-overlay__transparent-container {
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
}

.result-card__figure-overlay__transparent-container .icon-container {
  background-color: red;
}

.result-card__block {
  padding: 0.5em 0;
}

.result-card__block:first-child {
  padding-top: 0;
  margin-top: -0.25em;
}

.result-card__block + .result-card__block {
  margin-top: -1em;
}

.result-card__block:last-child {
  margin-bottom: -1em;
}

.result-card__block:last-child > *:last-child {
  margin-bottom: 0;
}

.result-card__block:last-child > *:last-child * + * {
  margin-bottom: 0;
}

.result-card__text {
  font-family: "Yale New Roman", Georgia, "Times New Roman", Times, serif;
  font-size: 1.125em;
  line-height: 1.25;
}

.result-card__title {
  font: 600 normal 16px/22.4px "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.125em;
  margin-bottom: 0;
  padding-left: 0.5em;
  padding-right: 0.5em;
}

.result-card__block > .result-card__title {
  padding-left: 0;
  padding-right: 0;
}



