@import '../../theme/variables';
.audio-player{
	display: flex;
	flex-direction: column;
	font-size: 1rem;
	margin: -1.9em 0.4em .2em;

	.embed-responsive {
		width: 100%;
	}
	.audio-player__controls{
		display: flex;
		min-height: em(75px);
	}

	.btn__vol,
	.btn__dl,
	.btn__play,
	.btn__pause{
		position: relative;
		display: inline-flex;
		// text-indent: -999em;
    border: 0;
		padding: 0;
		margin: 1em .5em;
		border-radius: 50%;
		background-color: $tearose;
		background-image: none;
		text-align: center;
	}
	.btn__vol,
	.btn__dl{
		width: 30px;
    height: 30px;
		background-color: white;
		border: 2px solid $tearose;
		svg {
			fill: $tearose;
			width: .75rem;
			position: absolute;
	    transform: translate(-50%, -50%);
	    top: 50%;
	    left: 50%;
		}
		&:hover{
			border-color: darken($tearose, 10%);
			svg{
				fill: darken($tearose, 10%);
			}
		}
	}
	.btn__play{
		width: 48px;
    height: 48px;
		svg {
			fill: white;
			width: 1rem;
			margin-left: auto;
			margin-right: auto;
			position: relative;
			left: 1px;
		}
		&:hover{
			background-color: darken($tearose, 10%);
		}
	}
	.btn__pause{
		width: 48px;
    height: 48px;
		svg {
			fill: white;
			width: 1rem;
			height: 1rem;
			margin-left: auto;
			margin-right: auto;
		}
		&:hover{
			background-color: darken($tearose, 10%);
		}
	}

	.btn__vol {
		position: relative;
		.vol-panel__container{
			position: absolute;
			top: 0; right:0;
			bottom: 0; left: 0;
			& > div{
		    visibility: hidden;
		    opacity: 0;
		    bottom: 47px;
		    transition-property: opacity, visibility;
		    transition-duration: 0s, 0s;
		    transition-delay: .1s, .2s;
		    z-index: 500;
		    margin: 0px 0 0px -4px;
		    height: 80px;
			}
			&:hover > div{
				display: block;
				opacity: 1;
				visibility: visible;
				transition-property: opacity, visibility;
				transition-duration: .2s, 0s;
				transition-delay: .1s, .2s;
			}
		}
	}
	.vol-panel{
		$border-width: em(4px);
		background-color: white;
		border: $border-width solid $tearose;
		position: absolute;
		width: 34px;
    height: 80px;
    bottom: $border-width;
		left: 0;
    border-radius: 30px;
	}
	.vol-panel__bar{
		background-color: $gray-lighter;
		display: block;
		width: em(4px);
		transform: translateX(-50%);
		left: 50%;
		margin: em(10px) auto;
	}
	.vol-panel__bar-0{
		background-color: darken($tearose, 10%);
	}
	.vol-panel__bar-1{
		background-color: lighten($tearose, 15%);
	}
	.vol-panel__handle{
		display: block;
		background: black;
		width: em(20px);
		height: em(20px);
		left: 50%;
		border-radius: 50%;
		transform: translateX(-50%);
		background-color: darken($tearose, 10%);
		border: 3px solid white;
	}
}



