@import '../../theme/variables';
@mixin box {
  border: 1px solid $azure;
  width: em(30px);
  height: em(30px);
  text-align: center;
  padding: 0;
  line-height: em(30px);
  margin: 0 0.3em;
  color: $azure;
  display: block;
}
.interactive-pagination {
  font-size: 1rem;
  font-weight: bold;
  margin-top: .65rem;
  ul li a:not([class]) {
    @include box
  }
}
.interactive-pagination > ul {
  margin: 0 auto;
}
.interactive-pagination__fa {
  @include box cursor: pointer;
}
.interactive-pagination__current-pagenumber {
  @include box;
  color: white;
  background-color: $azure;
}
.interactive-pagination__less-than-one-pagenumber {
  @include box;
  margin: 0 -0.4em;
  border: none;
  color: black;
}

.interactive-pagination__other-pagenumber{
  @include box;
}
.disabled-arrow {
display: none;
}



