/*

 Sass Functions
 */
.figure-and-caption {
  font-size: 1rem;
  clear: both;
  display: flex;
  flex-direction: column;
}

.figure-and-caption + .figure-and-caption {
  margin-top: 1em;
}

.figure-and-caption--left {
  float: left;
  margin-right: 1em;
  margin-bottom: .5em;
  text-align: left;
}

@media (max-width: 480px) {
  .figure-and-caption--left {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
    float: none;
  }
}

@media (min-width: 768px) {
  .figure-and-caption--left .figure-image {
    width: 21.8125em;
  }
}

@media (max-width: 768px) {
  .figure-and-caption--left .figure-image {
    width: 100%;
  }
}

.figure-and-caption--right {
  float: right;
  margin-left: 1em;
  margin-bottom: .5em;
  text-align: left;
}

@media (max-width: 480px) {
  .figure-and-caption--right {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
    float: none;
  }
}

@media (min-width: 768px) {
  .figure-and-caption--right .figure-image {
    width: 21.8125em;
  }
}

@media (max-width: 768px) {
  .figure-and-caption--right .figure-image {
    width: 100%;
  }
}

.figure-and-caption .figure-title {
  font-size: 1.75em;
}

.figure-and-caption .figcaption {
  clear: both;
  display: block;
  margin-top: -.5rem;
}

.figure-and-caption .figure-attribution {
  clear: both;
  float: right;
}

.figure-image {
  clear: both;
  display: block;
  margin-bottom: 1em;
}

.figure-image--left {
  float: left;
  margin-right: 1em;
  margin-bottom: .5em;
}

.figure-image--right {
  float: right;
  margin-left: 1em;
  margin-bottom: .5em;
}



