/*

 Sass Functions
 */
.module-topnav {
  display: block;
  font-size: 1rem;
  padding: 1em 0em 0;
}

.logo {
  margin: 0 0 1.25rem 0;
  font-size: 0;
  line-height: 1;
}

.logo h1 {
  margin: 0;
}

.top-nav__links {
  line-height: 1;
  margin-left: 0;
  margin-right: 0;
  position: relative;
  margin-bottom: 1.025rem;
  list-style: none;
  display: flex;
  flex-direction: row;
}

.top-nav__links .top-nav__link {
  text-decoration: none;
  padding-left: 0;
  padding-right: 0;
  font-size: 1.375em;
  padding-bottom: .7rem;
  margin-left: 8px;
  margin-right: 8px;
  color: #1a1a1a;
  border-bottom: 3px solid rgba(248, 163, 109, 0);
  transition: border-color .15s ease-out;
}

.top-nav__links .top-nav__link--active {
  border-color: #f8a36d;
}

.top-nav__links .top-nav__link:hover {
  border-color: #f8a36d;
}

@media (min-width: 768px) {
  .topnav_svg-icon--ym-wordmark {
    /*
    putting a lower value here because the current
    topnav from real data has many more elements. either need
    flexbox wrap or the overflow continues outside of the dedicated area, on the right side.
    */
    max-width: 326px;
  }
}

.topnav_svg-icon--ym-wordmark path {
  fill: #3489C9;
}



