@import '../../theme/variables';
$gutter-width: 0.5em;

.result-card{
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  padding: 0;
  flex-basis: 100%;
  max-width: 100%;
  border: 0;

  @media(min-width: $screen-sm){
    margin-bottom: em(40px);
  }
  @media(max-width: $screen-sm){
    margin-bottom: em(20px);
  }

  & > [class*="row-"],
  & > [class*=" row-"]{
    margin-left: -$gutter-width;
    margin-right: -$gutter-width;
  }
  .card-column--text{
    max-height: 140px;
  }
  .card-column--figure{
    flex-basis: calc(213px + #{$gutter-width * 2});
    max-width: calc(213px + #{$gutter-width * 2});
  }
}


.result-card__figure {
  position: relative;
}
.result-card__category-label{
  font-size: 0.750em;
  text-transform: uppercase;
  margin-top: -1em;
  display: inline-block;

  &:after{
    content: '';
    clear: both;
    display: block;
  }
}
.result-card__byline{
  font-size: 0.750em;
  margin-bottom: $gutter-width;
  margin-top: $gutter-width / 2;
}
.result-card__figure-overlay{
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1em;
  margin: -$gutter-width;
  align-items: flex-start;
  justify-content: flex-end;
}
.result-card__figure__icon{
  margin-top: 0em;
  font-size: 2em;
  vertical-align: text-top;
  line-height: 1.4;
  padding: 0 0.25em;
}

.result-card__figure-overlay__transparent-container{
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  .icon-container{
    background-color: red;
  }
}
.result-card__block{
  padding: $gutter-width 0;
  &:first-child{
    padding-top: 0;
    margin-top: -($gutter-width / 2);
  }
  & + & {
    margin-top: -1em;
  }
  &:last-child{
    margin-bottom: -1em;
  }
  &:last-child > *:last-child{
    margin-bottom: 0;
    * + *{
      margin-bottom: 0;
    }
  }
}
.result-card__text{
  font-family: $font-family-serif;
  font-size: 1.125em;
  line-height: $headings-line-height;
}
.result-card__title {
  font: $font_proxima-nova--semibold;
  font-size: 1.125em;
  margin-bottom: 0;
  padding-left: $gutter-width;
  padding-right: $gutter-width;
  .result-card__block > &{
    padding-left: 0;
    padding-right: 0;
  }
}



