@import '../../theme/variables';
.page-toolbar {
  display: flex;
  font-size: 1rem;
  width: 100%;
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  li {
    flex-direction: column;
    text-align: center;
    a {
      text-decoration: none;
    }
  }
  .btn-circle {
    cursor: pointer;
    margin: 0;
    width: 36px;
    height: 36px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: $seafoam;
    color: rgba(white, .9);
    &:hover {
      background-color: darken($seafoam, 5%);
      color: rgba(white, 1);
    }
    .fa {
      font-size: 0.875em;
      &.fa-envelope {
        font-size: 0.750em;
      }
    }
  }
  @media(min-width: $screen-sm) {
    // Desktop
    flex-direction: column;
    width: 100%;
    min-width: calc(108px);
    padding-left: $grid-gutter-width / 2;
    & > .page-toolbar__section {
      display: flex;
      flex-direction: row;
    }
    & > .page-toolbar__section + .page-toolbar__section {
      margin-top: 1.563em;
    }
    li + li {
      margin-top: 0.5em;
    }
  }
  @media(max-width: $screen-sm) {
    // Mobile
    display: flex;
    flex-direction: row;
    margin-bottom: 1em;
    section {
      display: inline-flex;
      flex-direction: column;
    }
    ul {
      display: inline-flex;
      align-items: flex-end;
    }
    ul ~ ul {
      margin-left: $grid-gutter-width;
    }
    li {
      display: inline-flex;
      padding-left: $grid-gutter-width / 4;
      padding-right: $grid-gutter-width / 4;
    }
  }
}



