@import '../../theme/variables';

.module-doctorcard {
  font-size: 1rem;
  display: flex;
  @media(min-width: $screen-sm){
    margin-bottom: 2em;
  }

  @media(min-width: $screen-sm){
    margin-bottom: 4em;
  }

  & > [class*="col-"],
  & > [class*=" col-"]{
    position: relative;
  }
  .panel{
    flex-basis: 100%;
    max-width: 100%;
    display: flex;
    flex: 1;
    flex-direction: row;
    margin-bottom: 0;
    justify-content: flex-start;
    align-items: stretch;
    align-content: space-between;
    background-color: lighten($azure, 37%);

    a{
      color: $azure;
    }
  }
  .panel-heading{
    display: flex;
    padding: .6em 0.75em;
    flex: 0 1 auto;
    background-color: $azure;
    color: white;
    font-size: 1.375em;
    & > span + span{
      margin-left: .24em;
    }
  }
  .panel-body{
    display: flex;
    padding: $panel-body-padding;
    flex: 0 1 100%;
    flex-direction: column;
  }

  .panel-body_heading{
    text-transform: uppercase;
    font-size: 0.750em;
    font-weight: bold;
  }

  .panel-body_text--large{
    font-size: 1.625em;
  }
  .panel-footer{
    display: flex;
    padding-left: 1em;
    padding-right: 1em;
    padding-bottom: .5em;

    flex: 0 1 auto;
  }

  .module-doctorcard__figure{
    max-width: 260px;
    position: relative;
    img{
      width: 100%;
      height: auto;
    }
  }
  .module-doctorcard__body{
    display: flex;
  }
  .semibold{
    font-weight: 600;
  }
}



