/*

 Sass Functions
 */
.takeaways-container {
  font-size: 1rem;
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  position: relative;
}

@media (min-width: 992px) {
  .takeaways-container {
    float: right;
    max-width: 50%;
    margin: 0 0 1em 1em;
  }
}

.takeaways-container ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.takeaways-container ol li {
  text-align: left;
  display: flex;
  flex-direction: row;
  background-color: #edf6fb;
  counter-increment: step-counter;
  font-size: 1em;
  padding: 1em 1.5em 1em 0;
}

.takeaways-container ol li:before {
  content: counter(step-counter);
  font-family: 'Yale New Bold Italic';
  font-size: 1.750em;
  line-height: 0.95em;
  min-width: 3.75rem;
  color: #52A9D9;
  text-align: center;
}

.takeaways-container ol li + li {
  margin-top: 1px;
}

.takeaways-container .panel {
  font-size: 1rem;
}

.takeaways-container .panel .panel-heading {
  background-color: #52A9D9;
  padding: 1em;
}

.takeaways-container .panel .panel-heading .panel-header {
  text-align: center;
  font-size: 1.5em;
  line-height: 1.16666667;
  color: white;
}

.takeaways-container .panel .panel-body {
  padding: 0;
  margin: 0;
}



