@import '../../theme/variables';

.search-field{
	margin: 2.813em 0;
	input{
		border: none;
	}
}

.search-field__btn{
	background-color: $azure;
	border: 1px solid $azure;
	&:hover{
		background-color: $azure;
		border: 1px solid $azure;
	}
	&:focus{
		background-color: $azure;
		border: 1px solid $azure;
	}
	&:visited{
		background-color: $azure;
		border: 1px solid $azure;
	}
	&:active{
		background-color: $azure;
		border: 1px solid $azure;
	}
}



