 */
/*

 Sass Functions
 */
.customized-accordion .panel-group {
  margin-bottom: 1rem;
}

.customized-accordion :global(.panel-default > .panel-heading) {
  background-color: transparent;
  padding-left: 0;
  padding-right: 0;
}

.customized-accordion :global(.panel-default > .panel-heading) .panel-title a {
  font-weight: 600;
  font-size: 1.375rem;
  font-weight: 600;
}

.customized-accordion :global(.panel-body) {
  background-color: #f6f6f6;
  border-top-color: transparent;
}

.customized-accordion :global(.panel-body) ol,
.customized-accordion :global(.panel-body) ul {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}

.customized-accordion :global(.panel-body) ol li + li,
.customized-accordion :global(.panel-body) ul li + li {
  margin-top: 0.5em;
}

.customized-accordion :global(.panel-body) *:last-child {
  margin-bottom: 0;
}

.customized-accordion :global(.panel-default > .panel-heading + .panel-collapse > .panel-body) {
  border-top-color: transparent;
}

.customized-accordion .accordion {
  font-size: 1rem;
}

.customized-accordion .accordion .accordion-panel {
  display: block;
  border-width: 0;
  box-shadow: none;
  background-color: transparent;
}

.customized-accordion .accordion .accordion-panel:hover a[aria-expanded]:after {
  background-color: #2d93cb;
}

.customized-accordion .accordion .accordion-panel a[aria-expanded] {
  font-weight: 600;
  font-size: 1.375rem;
  padding-right: 1.15em;
  display: block;
  position: relative;
  text-decoration: none;
  color: black;
}

.customized-accordion .accordion .accordion-panel a[aria-expanded]:after {
  content: '';
  transition: background-color .15s ease-out;
  position: absolute;
  right: 0;
  top: .5em;
  display: block;
  background-color: #52A9D9;
  font-size: 1rem;
  font-family: "proxima-nova", sans-serif;
  font-style: normal;
  font-weight: 700;
  text-align: center;
  width: 1.250em;
  height: 1.250em;
  line-height: 1.35em;
  color: white;
}

.customized-accordion .accordion .accordion-panel a[aria-expanded][aria-expanded="true"]:after {
  content: '–';
}

.customized-accordion .accordion .accordion-panel a[aria-expanded][aria-expanded="false"]:after {
  content: '+';
}

.customized-accordion .accordion .accordion-panel .panel-title a {
  font: 600 normal 16px/22.4px "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.375rem;
}

