.vertical-list--with-rule {
  display: flex;
  align-items: center;
  font-size: 1rem;
}

.vertical-list--with-rule > ul {
  display: flex;
  flex: 1;
  padding: 8px 0;
  margin-bottom: 0;
  list-style: none;
  flex-direction: column;
}

header ~ .vertical-list--with-rule > ul {
  padding-top: 0;
}

.vertical-list--with-rule > ul > li {
  flex: 1;
  padding: 1.6em 0;
}

.vertical-list--with-rule > ul > li + li {
  border-top: 1px solid #eee;
  padding: 1.6em 0;
}

@media (min-width: 768px) {
  .vertical-list--with-rule > ul > li + li {
    margin-top: 0;
  }
}

.vertical-list--with-rule > ul > li > a {
  display: flex;
  white-space: nowrap;
  padding: 8px;
}

.app .brand {
  position: absolute;
  top: 5px;
  left: 5px;
  display: inline-block;
  background: #2d2d2d url("../Home/logo.png") no-repeat center center;
  width: 40px;
  height: 40px;
  background-size: 80%;
  margin: 0 10px 0 0;
  border-radius: 20px;
}

.app :global(nav .fa) {
  font-size: 2em;
  line-height: 20px;
}



