@import '../../theme/variables';

.vertical-list--with-rule{
	display: flex;
	align-items: center;
	font-size: 1rem;
	& > ul{
		display: flex;
		flex: 1;
		padding: ($grid-gutter-width / 2) 0;
		margin-bottom: 0;
		list-style: none;
		flex-direction: column;

		header ~ &{
			padding-top: 0;
		}
	}
	& > ul > li{
		flex: 1;
		//padding: ($grid-gutter-width / 2) 0;
		padding: 1.6em 0;
		&:first-of-type{
			//padding-top: 0;
		}
	}
	& > ul > li + li{
		border-top: 1px solid #eee;
		padding: 1.6em 0;
		@media (min-width: $screen-sm){
			margin-top: 0;
		}
	}
	& > ul > li > a{
		display: flex;
		white-space: nowrap;
		padding: $grid-gutter-width / 2;
	}
}



