/*

 Sass Functions
 */
.toolbar__page-category-filter {
  background-color: #edf6fb;
  display: block;
  font-size: 1rem;
  margin-top: -1rem;
  margin-bottom: 1rem;
  padding: 1em 2.5em;
}

.toolbar__page-category-filter ul {
  display: flex;
  padding: 0;
  margin-bottom: 0;
  list-style: none;
}

@media (min-width: 480px) {
  .toolbar__page-category-filter ul {
    flex-direction: row;
  }
}

@media (max-width: 480px) {
  .toolbar__page-category-filter ul {
    flex-direction: column;
  }
}

.toolbar__page-category-filter ul li {
  visibility: visible;
}

@media (min-width: 480px) {
  .toolbar__page-category-filter ul li + .toolbar__page-category-filter ul li {
    margin-top: 0;
  }
}

.toolbar__page-category-filter ul li a {
  display: flex;
  white-space: nowrap;
  padding-left: 8px;
  padding-right: 8px;
}

.toolbar__page-category-filter p {
  margin: 0;
}

.label--large-text {
  font-size: 1.375em;
  position: relative;
  top: .1em;
}

.toolbar__alphabetical-filter {
  display: flex;
  flex-direction: row;
}

.toolbar__doctor-filter {
  display: flex;
  flex-direction: column;
}

.form__doctor-filter__well {
  padding: 1em 2.5em 1.5em 2.5em;
  margin: 0;
}

.form__doctor-filter__header {
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 0 auto;
  float: none;
}

.form__doctor-filter__header .doctors_results__h4 {
  margin: 1em 0;
}

.form__doctor-filter__col2 div {
  position: relative;
  display: inline-block;
  color: white;
  margin: 0.5em 0.5em 0 0.5em;
  width: 10.5em;
  font-weight: bold;
  overflow: hidden;
  background-color: #52A9D9;
  background: #52A9D9;
  cursor: pointer;
  padding: 0.2em 0;
}

.form__doctor-filter__col2 div select {
  padding: 5px 8px;
  width: 130%;
  border: none;
  box-shadow: none;
  background: transparent;
  background-image: none;
  -webkit-appearance: none;
}

.form__doctor-filter__col2 div i {
  position: absolute;
  right: 0;
  right: 0.5em;
  top: 0.7em;
}

.form__doctor-filter__col1 strong {
  position: relative;
  top: 0.1em;
}

.form__doctor-filter__col1 label {
  position: relative;
  margin: .5rem;
  font-family: Arial, sans-serif;
  line-height: 135%;
  cursor: pointer;
}

.form__doctor-filter__col1 label span {
  font-size: 0.9em;
  position: relative;
  left: 1em;
  top: 0.2em;
}

.form__doctor-filter__col3 label {
  position: relative;
  margin: .5rem;
  font-family: Arial, sans-serif;
  line-height: 135%;
  cursor: pointer;
}

.form__doctor-filter__col3 label span {
  font-size: 0.9em;
  position: relative;
  left: 1em;
  top: 0.2em;
}

.form__doctor-filter__col3 label span {
  left: 0.7em;
}

.form__doctor-filter__refine-search__checkbox {
  cursor: pointer;
  position: relative;
}

.form__doctor-filter__refine-search__checkbox:before {
  transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transform: rotate(-45deg) scale(0, 0);
  content: "";
  position: absolute;
  left: 0.4em;
  top: 0.3em;
  z-index: 1;
  width: 0.75em;
  height: 0.38em;
  border: 2px solid #52A9D9;
  border-top-style: none;
  border-right-style: none;
}

.form__doctor-filter__refine-search__checkbox:after {
  content: "";
  position: absolute;
  top: -0.125rem;
  left: 0;
  width: 1.5em;
  height: 1.5em;
  background: #edf6fb;
  border: 2px solid #52A9D9;
  cursor: pointer;
}

.form__doctor-filter__refine-search__checkbox:checked:before {
  transform: rotate(-45deg) scale(1, 1);
}



