@import '../../theme/variables';

@mixin states{
	background-color: $skyblue;
	color: #1a1a1a;
	text-decoration: none;
	a{
		color: #1a1a1a;
		text-decoration: none;
	}
}

.right-side-navigation{
	font-size: 1rem;
	ul{
		border:1px solid $gray-lighter;
		border-bottom: none;
		li{
			padding: 0;
			border-bottom: 1px solid $gray-lighter;
			border-left: 0.25em solid $azure;
			a:not([class]){
				color: #1a1a1a;
				padding: 0.625em 0 0.625em 0.93em;
			}
			&:hover{
				@include states;
			}
			&:active, &.active{
	    	  	background-color: $azure;
				color: white;
				text-decoration: none;
				border-left-color: $papaya;
				a{
					color: white;
					text-decoration: none;
					font-weight: bold;
				}
			}
		}
	}
}



