/*

 Sass Functions
 */
.vertical-list {
  font-size: 1rem;
}

.vertical-list > ul {
  flex: 1;
  margin: 0 0 8px 0;
  padding: 0;
  margin-bottom: 0;
  list-style: none;
  flex-direction: column;
}

header ~ .vertical-list > ul {
  padding-top: 0;
}

.vertical-list > ul > li {
  flex: 1;
  padding: 4px 0;
}

.vertical-list > ul > li:first-child {
  padding-top: 0;
}

.vertical-list > ul > li:last-child {
  padding-bottom: 0;
}

.vertical-list > ul > li + .vertical-list > ul > li {
  border-top: 1px solid #ccc;
}

@media (min-width: 768px) {
  .vertical-list > ul > li + .vertical-list > ul > li {
    margin-top: 0;
  }
}

.vertical-list > ul > li > a {
  display: flex;
  white-space: nowrap;
}

.vertical-list > ul > li > a .fa {
  display: inline-block;
  line-height: inherit;
}

.vertical-list > ul > li > a .fa:before, .vertical-list > ul > li > a .fa:after {
  content: ' ';
  display: inline;
}

.vertical-list > ul > li > a:not([class]) {
  padding: 4px 0;
}

.vertical-list li:empty {
  display: none;
}



