/*

 Sass Functions
 */
.media-modal-module {
  font-size: 1rem;
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  clear: both;
  width: 100%;
}

@media (min-width: 768px) {
  .media-modal-module {
    max-width: 50%;
    float: right;
    margin: .5rem 0 1rem 1rem;
  }
}

.media-modal-module__figure {
  font-size: 1rem;
}

.media-modal-module__caption {
  font-size: 1rem;
  margin-top: .5em;
}

.media-modal-module__btn-trigger {
  font-size: 1rem;
  display: inline-block;
  position: absolute;
  background-color: #52A9D9;
  text-align: center;
  width: 1.8125em;
  height: 1.8125em;
  color: white;
  bottom: 0;
  right: 0;
}

.media-modal-module__btn-trigger:after {
  content: "+";
  font-size: 1.750em;
  line-height: 1.1;
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 700;
}



