@import '../../theme/variables';

.body-text {
  font-size: 1rem;
  font-family: $font-family-serif;

  h1, h2, h3, h4, h5, h6 {
    font-family: $font-family-sans-serif;
    font-weight: $font_proxima-nova--regular-weight;
    line-height: $headings-line-height;
    font-size: em(28px);
    margin: 1rem 0;
  }
  p {
    font-family: $font-family-serif;
    font-size: em(20px);
  }
  li {
    font-size: 1.25em;
  }
}

.body-text__figure-and-caption {
  font-size: 1rem;
  margin-bottom: 1em;
  & + & {
    margin-top: em(70px);
  }
}
.body-text__figure-and-caption_caption{
  font-family: $font-family-serif;
  font-size: em(20px);
}
.body-text__figure-and-caption_title{
  font-family: $font-family-sans-serif;
  font-weight: $font_proxima-nova--regular-weight;
  line-height: $headings-line-height;
  margin: 1rem 0 .5rem;
}



