/*

 Sass Functions
 */
.vertical-list--with-rule {
  display: flex;
  align-items: center;
  font-size: 1rem;
}

.vertical-list--with-rule > ul {
  display: flex;
  flex: 1;
  padding: 8px 0;
  margin-bottom: 0;
  list-style: none;
  flex-direction: column;
}

header ~ .vertical-list--with-rule > ul {
  padding-top: 0;
}

.vertical-list--with-rule > ul > li {
  flex: 1;
  padding: 1.6em 0;
}

.vertical-list--with-rule > ul > li + li {
  border-top: 1px solid #eee;
  padding: 1.6em 0;
}

@media (min-width: 768px) {
  .vertical-list--with-rule > ul > li + li {
    margin-top: 0;
  }
}

.vertical-list--with-rule > ul > li > a {
  display: flex;
  white-space: nowrap;
  padding: 8px;
}



