@import '../../theme/variables';
$gutter-width: 1em;

.section-header-align {
  text-align: center;
}
.section-header {
  display: flex;
  flex-direction: column;
  border-top: 3px solid $azure;
  font-size: 1rem;
  padding: 0.413em 0;
  margin: 0;
  h1,
  h2,
  h3,
  h4,
  h5 {
    font-family: 'Yale New Roman', 'Times', 'Times New Roman', serif;
    line-height: $headings-line-height;
    color: black;
  }
  main[data-role="main-body"] & {
    h1,
    h2,
    h3,
    h4,
    h5 {
      font-size: 2.25em;
    }
  }
  aside[data-role="main-sidebar"] & {
    h1,
    h2,
    h3,
    h4,
    h5 {
      font-size: 1.75em;
    }
  }
}
.section-header--no-stroke{
  @extend .section-header;
  border-top: 0;
  padding: 0;
  margin-top: -0.15em;
}



