@import '../../theme/variables';

.media-modal-module {
  font-size: 1rem;
  font-family: $font-family-sans-serif;
  clear: both;
  width: 100%;
  @media(min-width: $screen-sm){
    max-width: 50%;
    float: right;
    margin: .5rem 0 1rem 1rem;
  }
}

.media-modal-module__figure{
  font-size: 1rem;
}
.media-modal-module__caption{
  font-size: 1rem;
  margin-top: .5em;
}
.media-modal-module__btn-trigger{
  font-size: 1rem;

  display: inline-block;
  position: absolute;
  background-color: $azure;
  text-align: center;
  width: em(29px);
  height: em(29px);

  color: white;
  bottom: 0;
  right: 0;

  &:after{
    content: "+";
    font-size: 1.750em;
    line-height: 1.1;
    font-family: $font-family-sans-serif;
    font-weight: $font_proxima-nova--bold-weight;
  }
}



