@import '../../theme/variables';
$gutter-width: 0.5em;
.card-group{
  display: flex;
}
.media-card {
  font-size: 1rem;
  display: flex;
  width: 100%;
  flex-direction: column;
  // margin-bottom: 1.250em;
  padding: $gutter-width;
}
.media-card__figure {
  margin-top: -$gutter-width;
  margin-left: -$gutter-width;
  margin-bottom: 0;
  margin-right: -$gutter-width;
}
.media-card__figure-overlay {
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: $gutter-width;
  margin: -$gutter-width;
  align-items: flex-start;
  justify-content: flex-end;
}
.media-card__figure + .media-card__banner {
  margin-top: 0;
  margin-bottom: $gutter-width;
}
.media-card__figure + .media-card-block{
  padding-top: $gutter-width;
}
.media-card__figure + .media-card__body > .media-card-block:first-child{
  padding-top: ($gutter-width * 2);
}
.media-card__body {
  position: relative;
}
.media-card__banner {
  font-weight: $font_proxima-nova--semibold-weight;
  margin-left: -$gutter-width;
  margin-right: -$gutter-width;
  padding: ($gutter-width - .1) ($gutter-width * 2);
  background-color: #333;
  color: white;
}
.media-card__figure-overlay__transparent-container {
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
}
.media-card-block {
  padding: $gutter-width;
  & + & {
    margin-top: -$gutter-width;
  }
  &:last-child > *:last-child {
    margin-bottom: 0;
    * + * {
      margin-bottom: 0;
    }
  }
}

.media-card__title {
  font: $font_proxima-nova--semibold;
  font-size: 1.250em;
  line-height: $headings-line-height;
  padding-left: $gutter-width;
  padding-right: $gutter-width;
  .media-card-block > & {
    padding-left: 0;
    padding-right: 0;
  }
}
.media-card--xl .media-card-text {
  margin-top: -.2em;
  font-family: $font-family-serif;
  font-size: em(18px);
}

.media-card--xl .media-card__title{
  font-size: 1.625em;
}
.media-card__subtitle {
  margin-top: -($gutter-width / 2);
  margin-bottom: 0;
}
.media-card__category-label {
  position: absolute;

  font-weight: $font_proxima-nova--semibold-weight;
  font-size: 0.750em;
  padding: ($gutter-width) ($gutter-width * 1.75);
  background-color: $papaya;
  text-transform: uppercase;
  color: white;

  transform: translateX(-0.5rem) translateY(-100%);
}
.media-card__byline {
  font-size: 0.75em;
  margin: -0.5em 0 0em;
}
.media-card-text:last-child {
  margin-bottom: 0;
}
.media-card-actions {
  padding: $gutter-width;
  .media-card-link + .media-card-link {
    margin-left: $gutter-width;
  }
}
.media-card-link {
  & + .media-card-link {
    margin-left: $gutter-width;
  }
}

.media-card-content {
  color: black;
  background-color: white;
  margin: 0 -0.5em -0.5em;
}

.media-card_solid--azure .media-card-content a {
  color: #{$azure} !important;
}

.media-card_solid--ymblue .media-card-content a {
  color: #{$ymblue} !important;
}

.media-card--largeSerifHeadline .media-card__title{
  font-family: $font-family-serif;
  font-weight: 400;
  font-size: 2em;
  line-height: $headings-line-height;
}

$themes: (
  'tearose': $tearose,
  'ymblue': $ymblue,
  'azure': $azure,
  'clover': $clover
);
@each $name, $color in $themes {
  // Could these following be .card__solid, etc? or should they remain media-card?
  .media-card_solid--#{$name} {
    .media-card__subtitle,
    .media-card__title {
      color: white;
    }
    .media-card__byline {
      color: lighten($color, 95%);
    }
    svg {
      fill: white;
    }
    a {
      color: white;
    }
  }
  .media-card_accented--#{$name} {
    border: 1px solid $border-color;
    .media-card__banner {
      background-color: #{$color};
      color: white;
    }
    svg {
      fill: #{$color};
    }
    .media-card-footer {
      a {
        color: #{$color};
      }
    }
  }
}



