/*

 Sass Functions
 */
.page-toolbar {
  display: flex;
  font-size: 1rem;
  width: 100%;
}

.page-toolbar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.page-toolbar li {
  flex-direction: column;
  text-align: center;
}

.page-toolbar li a {
  text-decoration: none;
}

.page-toolbar .btn-circle {
  cursor: pointer;
  margin: 0;
  width: 36px;
  height: 36px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: #83C4B9;
  color: rgba(255, 255, 255, 0.9);
}

.page-toolbar .btn-circle:hover {
  background-color: #72bcaf;
  color: white;
}

.page-toolbar .btn-circle .fa {
  font-size: 0.875em;
}

.page-toolbar .btn-circle .fa.fa-envelope {
  font-size: 0.750em;
}

@media (min-width: 768px) {
  .page-toolbar {
    flex-direction: column;
    width: 100%;
    min-width: calc(108px);
    padding-left: 8px;
  }
  .page-toolbar > .page-toolbar__section {
    display: flex;
    flex-direction: row;
  }
  .page-toolbar > .page-toolbar__section + .page-toolbar__section {
    margin-top: 1.563em;
  }
  .page-toolbar li + li {
    margin-top: 0.5em;
  }
}

@media (max-width: 768px) {
  .page-toolbar {
    display: flex;
    flex-direction: row;
    margin-bottom: 1em;
  }
  .page-toolbar section {
    display: inline-flex;
    flex-direction: column;
  }
  .page-toolbar ul {
    display: inline-flex;
    align-items: flex-end;
  }
  .page-toolbar ul ~ ul {
    margin-left: 16px;
  }
  .page-toolbar li {
    display: inline-flex;
    padding-left: 4px;
    padding-right: 4px;
  }
}



