@import '../../theme/variables';
.horizontal-list{
	display: flex;
	align-items: center;
	font-size: 1rem;
	ul{
		display: flex;
		padding: 0;
		margin-bottom: 0;
		list-style: none;

		@media (min-width: $screen-xs){
			flex-direction: row;
		}
		@media (max-width: $screen-xs){
			flex-direction: column;
		}
	}
	ul li{
		// commenting this out fixes the top nav display in safari
		// flex: 1;
		& + &{
			@media (min-width: $screen-xs){
				margin-top: 0;
			}
		}
	}
	ul li a:not([class]){
		display: flex;
		white-space: nowrap;
		padding-left: $grid-gutter-width / 2;
		padding-right: $grid-gutter-width / 2;
	}
}
.no-side-padding{
	padding-left: 0;
	padding-right: 0;
}



