@import '../../theme/variables';
.takeaways-container {
  font-size: 1rem;
  font-family: $font-family-sans-serif;
  position: relative;
  @media(min-width: $screen-md){
    float: right;
    max-width: 50%;
    margin: 0 0 1em 1em;
  }
  ol {
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
      text-align: left;
      display: flex;
      flex-direction: row;
      background-color: lighten($azure, 37%);
      counter-increment: step-counter;
      font-size: 1em;
      padding: 1em 1.5em 1em 0;
      &:before {
        content: counter(step-counter);
        font-family: 'Yale New Bold Italic';
        font-size: 1.750em;
        line-height: 0.95em;
        min-width: 3.75rem;
        color: $azure;
        text-align: center;
      }
    }
    li + li {
      margin-top: 1px;
    }
  }
  .panel{
    font-size: 1rem;
    .panel-heading{
      background-color: $azure;
      padding: 1em;
      .panel-header{
        text-align: center;
        font-size: 1.5em;
        line-height: 1.16666667;
        color: white;
      }
    }
    .panel-body{
      padding: 0;
      margin: 0;
    }
  }
}



