@import '../../theme/variables';
$gutter-width: 0.5em;
.headline-card {
  font-size: 1rem;
  display: flex;
  width: 100%;
  flex-direction: column;
  padding: $gutter-width;
}

.headline-card-block {
  padding: $gutter-width;
  & + & {
    margin-top: -$gutter-width;
  }
  &:last-child > *:last-child {
    margin-bottom: 0;
    * + * {
      margin-bottom: 0;
    }
  }
}

.headline-card__title {
  font-size: 1.250em;
  line-height: $headings-line-height;
  padding-top: $gutter-width;
  padding-left: $gutter-width;
  padding-right: $gutter-width;
  .media-card-block > & {
    padding-left: 0;
    padding-right: 0;
  }
}

.headline-card--largeSerifHeadline .headline-card__title{
  font-family: $font-family-serif;
  font-weight: 400;
  font-size: 2em;
  line-height: $headings-line-height;
}

$themes: (
  'tearose': $tearose,
  'ymblue': $ymblue,
  'azure': $azure,
  'clover': $clover
);
@each $name, $color in $themes {
  .headline-card_solid--#{$name} {
    .headline-card__title {
      color: white;
    }
    a {
      color: white;
    }
  }
  .headline-card_accented--#{$name} {
    border: 1px solid $border-color;
  }
}



