@import '../../theme/variables';

.locations-list{
  display: block;

  ul > li:last-child {
    padding: 0;
  }
}
// set max-height for MediaResponsiveEmbed map for location cards that span entire width of main column
.location-card--solo{
  [data-role="main-body"] & figure{
    @media(min-width: $screen-sm){
      padding-bottom: calc(((2 / 3) * 50%) - (#{$grid-gutter-width--em / 2}));
    }
  }
}



