@import '../../theme/variables';
$gutter-width: 0.5em;

.embed-responsive{
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;
}

.embed-responsive-1by1{
	padding-bottom: 100%;
}
.embed-responsive-3by2{ // 66.66666667%
  padding-bottom: calc((2 / 3) * 100%);
}
.embed-responsive-4by3{ // 75%
	padding-bottom: calc((3 / 4) * 100%);
}
.embed-responsive-4by3ish{ // 66%
  padding-bottom: calc((2.64 / 4) * 100%);
}
.embed-responsive-16by9{ // 56%
  padding-bottom: calc((9 / 16) * 100%);
}
.embed-responsive-portrait{ // 1.2300123001%
  padding-bottom: calc((5 / 4.065) * 100%);
}

.embed-responsive-item{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.embed-responsive-item__figure {
  margin-top: -$gutter-width;
  margin-left: -$gutter-width;
  margin-bottom: 0;
  margin-right: -$gutter-width;
}
.embed-responsive-item__figure-overlay {
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: $gutter-width;
  margin: -$gutter-width;
  align-items: flex-start;
  justify-content: flex-end;
}
.embed-responsive-item__figure__media-icon {
  display: inline-flex;
  font-size: 1em;
  background-color: $ymblue;
  width: 2.125em;
  height: 2.125em;

  margin: 0.750em;

  padding: 0 0.35em;

  border-radius: 50%;
  align-items: center;
  justify-content: center;
  & > svg.media-icon__svg {
    width: 1em;
    fill: white;
  }
}

.figure-title{
  font-size: em(22px);
  font-family: $font-family-sans-serif;
  font-weight: $font_proxima-nova--semibold-weight;
}



