@import '../../theme/variables';
.media-modal {
  font-size: 1rem;
  padding: 1em;
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: row;
  align-items: center;
  .modal-header {
    display: block;
    padding: 1rem 1rem 12px;
  }
  .modal-content {
    display: block;
    width: 100%;
  }
  .modal-footer {
    display: block;
  }
}
.modal-backdrop {
  display: none;
}
.modal-backdrop.in {
  display: block;
}



