@import '../../theme/variables';
// $gutter-width: .5em;
$gutter-width: 1rem;

.module-sidebarbox{
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  padding: 1em;
  ul, ol{
    font-size: 1em;
    line-height: $headings-line-height;
    padding-left: $gutter-width * 2;
    margin: 0;
    li{
      font-size: 1.125em;
    }
    li + li {
      padding-top: 0.5em;
    }
  }
  .module-sidebarbox-footer{
    margin: ($gutter-width) 0 ($gutter-width * 2);
  }
}

.module-sidebarbox__figure {
  margin-top: -$gutter-width;
  margin-left: -$gutter-width;
  margin-bottom: 0;
  margin-right: -$gutter-width;
}

.module-sidebarbox-banner{
  margin: 0 -$gutter-width;
  padding: ($gutter-width / 2) ($gutter-width * 2);
  background-color: #333;
  color: white;
}

.module-sidebarbox-block{
  padding: $gutter-width;
  &--title-container {
    margin-top: $gutter-width * 2;

  }
  p{
    font-size: 1.25rem;
    margin-bottom: 0.5em;
  }
  & + & {
    margin-top: -$grid-gutter-width;
  }

  &:last-child > *:last-child{
    margin-bottom: 0;
    * + *{
      margin-bottom: 0;
    }
  }
}

.module-sidebarbox__hr {
  margin-top: 1rem;
  margin-bottom: 0.4rem;
  margin-left: $gutter-width;
  margin-right: $gutter-width;
}
.module-sidebarbox__title {
  font-size: em(24px);
  font-weight: $font_proxima-nova--semibold-weight;
  margin-bottom: 0;
  padding-left: $gutter-width;
  padding-right: $gutter-width;
  .module-sidebarbox-block > &{
    padding-left: 0;
    padding-right: 0;
  }
}

.module-sidebarbox-subtitle {
  margin-top: -($gutter-width / 2);
  margin-bottom: 0;
}

.module-sidebarbox-text:last-child {
  margin-bottom: 0;
}

.module-sidebarbox-actions {
  padding: $gutter-width $gutter-width;

  .module-sidebarbox-link + .module-sidebarbox-link {
    margin-left: $gutter-width;
  }
}

.module-sidebarbox-link {
  & + .module-sidebarbox-link {
    margin-left: $gutter-width;
  }
}

.tint-grey{
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: lightgrey;
  color: white;
}

.module-sidebarbox{
  .lead{
    margin-bottom: 0;
  }
}



