@import '../../theme/variables';

.button-bar{
  margin-bottom: -.5rem;
  .horizontal-list ul li a{
    background-color: red;
    padding-left: $grid-gutter-width / 2;
		padding-right: $grid-gutter-width / 2;
    margin-bottom: .5rem;
  }
  .row--half-gutter .button-bar__block{
    height: .5rem;
  }
  .row .button-bar__block{
    height: 1rem;
  }
}
.button-bar__button{
  margin-bottom: .5rem;
}



