@import '../../theme/variables';

.module-globalsearchbar{
  display: block;
  background-color: $ymblue;
  font-size: 1rem;
  padding: 1.25em 0;
  margin-bottom: 50px;

  ::-webkit-input-placeholder {
   color: rgba(255,255,255,.75);
  }

  :-moz-placeholder {
     color: rgba(255,255,255,.75);
  }

  ::-moz-placeholder {
     color: rgba(255,255,255,.75);
  }

  :-ms-input-placeholder {
     color: rgba(255,255,255,.75);
  }
}
.input-group, .input-group-addon, .search-input{
  background-color: transparent;
  color: white;
  border: none;
  box-shadow: none;

  &:focus{
    box-shadow: none;
  }
}
.search-input{
  padding-left: .5em;
  padding-right: .5em;
  padding-top: 0;
  padding-bottom: 0;
  line-height: 39px;
  font-size: 1.750em;
  font-family: "proxima-nova",sans-serif;
  font-style: normal;
  font-weight: 300;

}

svg{
  fill: white;
}



