@import '../../theme/variables';
@mixin label{
  position: relative;
  margin: .5rem;
  font-family: Arial, sans-serif;
  line-height: 135%;
  cursor: pointer;
  span{
    font-size: 0.9em;
    position: relative;
    left: 1em;
    top: 0.2em;
  }
}

.toolbar__page-category-filter{
  background-color: $azure-light;
	display: block;
	font-size: 1rem;
	margin-top: -1rem;
	margin-bottom: 1rem;
	padding: 1em 2.5em;
	ul{
		display: flex;
		padding: 0;
		margin-bottom: 0;
		list-style: none;

		@media (min-width: $screen-xs){
			flex-direction: row;
		}
		@media (max-width: $screen-xs){
			flex-direction: column;
		}
	}
	ul li{
		// commenting this out fixes the top nav display in safari
		visibility: visible;
		& + &{
			@media (min-width: $screen-xs){
				margin-top: 0;
			}
		}
	}
	ul li a{
		display: flex;
		white-space: nowrap;
		padding-left: $grid-gutter-width / 2;
		padding-right: $grid-gutter-width / 2;
	}
	p {
		margin: 0;
	}
}
.label--large-text{
  font-size: em(22px);
  position: relative;
  top: .1em;
}
.toolbar__alphabetical-filter{
	display: flex;
	flex-direction: row;
}

.toolbar__doctor-filter{
	display: flex;
	flex-direction: column;
}


.form__doctor-filter__well {
  padding: 1em 2.5em 1.5em 2.5em;
  margin: 0;
}

.form__doctor-filter__header {
  font-family: $font-family-sans-serif;
  margin: 0 auto;
  float: none;
  .doctors_results__h4{
    margin: 1em 0;
  }
}


.form__doctor-filter__col2{
  div{
    position: relative;
    display: inline-block;
    color: white;
    margin: 0.5em 0.5em 0 0.5em;
    width: 10.5em;
    font-weight: bold;
    overflow: hidden;
    background-color: $azure;
    background: $azure;
    cursor: pointer;
    padding: 0.2em 0;
    select {
      padding: 5px 8px;
      width: 130%;
      border: none;
      box-shadow: none;
      background: transparent;
      background-image: none;
      -webkit-appearance: none;
   }
   i{
    position: absolute;
    right: 0;
    right: 0.5em;
    top: 0.7em;
   }

  }

}

.form__doctor-filter__col1{
  strong{
    position: relative;
    top: 0.1em;
  }
  label{
   @include label
  }
}

.form__doctor-filter__col3{
  label{
  @include label
  span{
    left: 0.7em;
    }
  }

}


.form__doctor-filter__refine-search__checkbox {
  cursor: pointer;
  position: relative;
  &:before {
  transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transform: rotate(-45deg) scale(0, 0);
  content: "";
  position: absolute;
  left: 0.4em;
  top: 0.3em;
  z-index: 1;
  width: 0.75em;
  height: 0.38em;
  border: 2px solid $azure;
  border-top-style: none;
  border-right-style: none;
  }
  &:after{
    content: "";
    position: absolute;
    top: -0.125rem;
    left: 0;
    width: 1.5em;
    height: 1.5em;
    background: #edf6fb;
    border: 2px solid $azure;
    cursor: pointer;
  }
  &:checked:before {
    transform: rotate(-45deg) scale(1, 1);
 }
}



