@import '../../theme/variables';
$gutter-width: 0.5em;
.interactive-audio{
  font-size: 1rem;
}
.interactive-audio__title {
  text-align: center;
  font-family: $font-family-serif;
  font-size: 1.250em;
  line-height: $headings-line-height;
  padding: ($gutter-width * 2) ($gutter-width * 2) ($gutter-width/2) ($gutter-width * 2);
}
.interactive-audio__banner {
  text-align: center;
  font-weight: $font_proxima-nova--regular-weight;
  font-size: 0.875em;
  margin-left: -$gutter-width;
  margin-right: -$gutter-width;
  margin-bottom: $gutter-width * 2;
  padding: ($gutter-width - .1) ($gutter-width * 2);
  color: white;
}
.interactive-audio__figure {
  margin-top: -$gutter-width;
  margin-left: -$gutter-width;
  margin-bottom: 0;
  margin-right: -$gutter-width;
}
.interactive-audio__player-container {
  display: flex;
  padding: 0;
  margin: 0;
  align-items: inherit;
  justify-content: inherit;
	background-color: white;
	color: black;
}

.interactive-audio-embed{
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 0px em(26px);
  background-color: white;
  margin: -$gutter-width / 2;
  .audio-player {
    margin-bottom: 0;
  }
}

.interactive-audio__duration{
  color: #000;
  margin: 1em .2em .2em;
}



