@import '../../theme/variables'; // NOTE Setting global class because of
.customized-accordion {
  .panel-group{
    margin-bottom: 1rem;
  }
  :global(.panel-default > .panel-heading) {
    background-color: transparent;
    padding-left: 0;
    padding-right: 0;
    .panel-title a {
      font-weight: $font_proxima-nova--semibold-weight;
      font-size: 1.375rem;
      font-weight: 600;
    }
  }

  :global(.panel-body) {
    background-color: #f6f6f6;
    border-top-color: transparent;
    ol,
    ul {
      list-style: none;
      padding-left: 0;
      padding-right: 0;
      li + li {
        margin-top: 0.5em;
      }
    }
    *:last-child {
      margin-bottom: 0;
    }
  }

  :global(.panel-default > .panel-heading + .panel-collapse > .panel-body) {
    border-top-color: transparent;
  }
  .accordion {
    font-size: 1rem;
    .accordion-panel {
      display: block;
      border-width: 0;
      box-shadow: none;
      background-color: transparent;
      &:hover a[aria-expanded]:after{
        background-color: darken($azure, 10%);
      }
      // Heading links/triggers
      a[aria-expanded] {
        font-weight: $font_proxima-nova--semibold-weight;
        font-size: 1.375rem;
        padding-right: 1.15em;
        display: block;
        position: relative;
        text-decoration: none;
        color: black;
        &:after {
          content: '';
          transition: background-color .15s ease-out;
          position: absolute;
          right: 0;
          top: .5em;
          display: block;
          background-color: $azure;
          font-size: 1rem;
          font-family: "proxima-nova", sans-serif;
          font-style: normal;
          font-weight: 700;
          text-align: center;
          width: 1.250em;
          height: 1.250em;
          line-height: 1.35em;
          color: white;
        }
        &[aria-expanded="true"] {
          &:after {
            content: '–';
          }
        }
        &[aria-expanded="false"] {
          &:after {
            content: '+';
          }
        }

      }
      .panel-title a {
        font: $font_proxima-nova--semibold;
        font-size: 1.375rem;
      }
    }
  }
}



