@import '../../theme/variables';
.toolbar-container{
	display: flex;
	flex-direction: column;
	font-size: 1rem;
	margin-top: 1rem;
	margin-bottom: 1rem;
	ul{
		display: flex;
		padding: 0;
		margin-bottom: 0;
		list-style: none;
		font-size: 1rem;
		@media (min-width: $screen-xs){
			flex-direction: row;
		}
		@media (max-width: $screen-xs){
			flex-direction: column;
		}
	}
	ul li{
		font-size: inherit;
		& + &{
			@media (min-width: $screen-xs){
				margin-top: 0;
			}
		}
	}
	ul li a{
		display: flex;
		white-space: nowrap;
		padding-left: $grid-gutter-width / 2;
		padding-right: $grid-gutter-width / 2;
	}
	p {
		margin: 0;
	}
}



