@import '../../theme/variables';
.module-videoplayer{
  font-size: 1rem;
  display: flex;
  flex-direction: column;
}
.module-videoplayer,
.module-videoplayer__container,
.module-videoplayer__caption{
  font-size: 1rem;
  display: flex;
  flex-direction: column;

  p {
    margin: 0;
  }
  
  p + p {
    margin-top: 1em;
  }
}
.module-videoplayer__container {
  position: relative;
}

.module-videoplayer__popup {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: $ymblue;
  padding: 1.5rem;
  color: white;
  width: 100%;
  max-width: 400px;

  header{
    font-size: 1rem;
    border-bottom: 1px solid rgba(white, .5);
    padding-bottom: 0.5em;
    h1,h2,h3,h4,h5,h6{
      font-family: $font-family-serif;
      font-size: 1.750em;
    }
  }

  .module-videoplayer__popup-link{
    text-decoration: none;
    font-family: $font-family-sans-serif;
    font-weight: $font_proxima-nova--semibold-weight;
    font-size: 1.125em;
    color: white;
  }
}



