@import '../../theme/variables';
$gutter-width: 1em;

.location-card{
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
  padding: 0;

  address {
    margin-bottom: 0;
  }
}

.label-caption {
  font-weight: bold;
}

.location-card__figure {
  margin: 0
}
.location-card__figure-overlay{
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1em;
  margin: -$gutter-width;
}
.location-card__figure__media-icon{
  margin-top: 0em;
  font-size: 2em;
  vertical-align: text-top;
  line-height: 1.4;
  padding: 0 0.25em;
}
.location-card__figure + .location-card-banner {
  margin-top: 0;
  margin-bottom: 0;
}
.location-card-banner{
  margin: 0;
  padding: ($gutter-width / 2) 0;
  background-color: #333;
  color: white;
}

.location-card__figure-overlay__transparent-container{
  text-align: center;
}
.location-card-block{
  padding: $gutter-width;

  & + & {
    margin-top: -1em;
  }

  &:last-child > *:last-child{
    margin-bottom: 0;
    * + *{
      margin-bottom: 0;
    }
  }
}
.location-card-title {
  margin-bottom: $gutter-width;
  padding-left: $gutter-width;
  padding-right: $gutter-width;
  .location-card-block > &{
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 0;
  }
  .location-card-banner > &{
    margin-bottom: 0;
    padding-left: $gutter-width + .1;
    padding-right: $gutter-width + .1;
  }
}

.location-card-subtitle {
  margin-top: -($gutter-width / 2);
  margin-bottom: 0;
}

.location-card-text:last-child {
  margin-bottom: 0;
}

.location-card-actions {
  padding: $gutter-width $gutter-width;

  .location-card-link + .location-card-link {
    margin-left: $gutter-width;
  }
}

// Apply left border to location card action links, if the cells aren't empty
.location-card__divided-list--horizontal > div:not(:empty) + div:not(:empty){
  &:before{
    content: "";
    top: 2px;
    position: relative;
    display: inline-block;
    height: 0.8em;
    width: 0px;
    border-left: 1px solid black;
    margin-left: -0.6em;
    margin-right: 0.6em;
    font-size: 1rem;

  }
}

.location-card-link {
  & + .location-card-link {
    margin-left: $gutter-width;
  }
}

$themes: (
  'ymblue': $ymblue,
  'azure': $azure
);
@each $name, $color in $themes {
  .location-card_solid--#{$name} {
    background-color: #{$color};
    color: white;

    .location-card-title, .location-card-subtitle{
      color: white;
    }
    svg{
      fill: white;
    }
    a {
      color: white;
    }
  }
  .location-card_accented--#{$name} {
    border: 1px solid $border-color;
    .location-card-banner{
      background-color: #{$color};
      color: white;
    }
    svg {
      fill: #{$color};
    }
    .location-card-footer{
      a {
        color: #{$color};
      }
    }
  }
}



