/*

 Sass Functions
 */
.footer_svg-icon--ym-wordmark {
  margin-left: -.2em;
}

.footer_svg-icon--ym-wordmark path {
  fill: #fff;
}

@media (min-width: 768px) {
  .footer_svg-icon--ym-wordmark {
    width: 200px;
    height: auto;
  }
}

@media (max-width: 768px) {
  .footer_svg-icon--ym-wordmark {
    width: 100%;
    height: auto;
  }
}

.module-footer {
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  margin-top: 3.125em;
  color: white;
}

.module-footer .social-link {
  display: inline-block;
  padding-left: .313em;
  padding-right: .313em;
}

.module-footer .svg-icon {
  width: 2.250em;
  fill: white;
}

.module-footer .lead {
  font-size: 1.750em;
  font-weight: 400;
}

.module-footer .primary-row {
  padding: 32px 0;
}

.module-footer .primary-row a {
  color: white;
  text-decoration: none;
}

.module-footer .secondary-row {
  font-size: 0.875em;
  padding: 8px 0;
  color: black;
}

.module-footer .secondary-row a {
  font-size: 0.875em;
  color: black;
}

.module-footer .secondary-row [class^="horizontal-list"] {
  margin-left: -.25em;
  margin-right: -.25em;
}

.module-footer .side-column {
  font-size: 1rem;
  display: flex;
}

.module-footer .main-column {
  display: flex;
  flex: 1 0 auto;
}

@media (min-width: 768px) {
  .module-footer .main-column ul:not(:last-child) {
    padding-right: 16px;
  }
}

.module-footer .link-list__column {
  font-size: 1rem;
  padding: 0;
}

@media (min-width: 768px) {
  .module-footer .link-list__column + .link-list__column {
    padding-right: 0;
    padding-left: 3em;
  }
}

.module-footer .link-list {
  display: flex;
  flex-direction: column;
}

@media (max-width: 768px) {
  .module-footer .link-list {
    margin-bottom: 2em;
  }
}

.module-footer .link-list li {
  padding: 0;
}

.module-footer .link-list li a {
  white-space: nowrap;
  padding: 0.125em 0;
}

.module-footer header {
  font-weight: 600;
  margin-bottom: .5em;
  font-size: 1.375em;
  white-space: nowrap;
  margin-top: 0.3em;
}

.module-footer .header-text--smallcaps {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 0.750em;
  white-space: nowrap;
  margin-bottom: 0.875em;
}

.bg {
  background-color: #3489C9;
}

.container-fluid--no-padding {
  padding: 0;
}

.horizontal-list--vertical-rule {
  display: inline-flex;
}

.horizontal-list--vertical-rule li {
  position: relative;
}

@media (min-width: 768px) {
  .horizontal-list--vertical-rule li + li:before {
    content: '';
    display: inline-block;
    height: 0.625em;
    width: 1px;
    background-color: #d9d9d9;
    position: absolute;
    transform: translateY(56%);
    left: 0;
  }
}



