@import '../../theme/variables';

.module-topnav{
  display: block;
  font-size: 1rem;
  padding: 1em 0em 0;
}
.logo {
  margin: 0 0 1.25rem 0;
  font-size: 0;
  line-height: 1;

  h1 {
    margin: 0;
  }
}
.top-nav__links{
  line-height: 1;
  margin-left: 0;
  margin-right: 0;
  position: relative;
  margin-bottom: 1.025rem;
  list-style: none;
  display: flex;
  flex-direction: row;

  .top-nav__link{
    text-decoration: none;
    padding-left:0;
    padding-right:0;
    font-size: #{22px/$font-size-base}em;

    padding-bottom: .7rem;

    margin-left: $grid-gutter-width / 2;
    margin-right: $grid-gutter-width / 2;

    color: #1a1a1a;
    border-bottom: 3px solid rgba($papaya, 0);
    transition: border-color .15s ease-out;
    &--active{
      border-color: rgba($papaya, 1);
    }
    &:hover{
      border-color: rgba($papaya, 1);
    }
  }
}

.topnav_svg-icon--ym-wordmark{
  @media(min-width: $screen-sm){
    /*
    putting a lower value here because the current
    topnav from real data has many more elements. either need
    flexbox wrap or the overflow continues outside of the dedicated area, on the right side.
    */
    max-width: 326px;
  }
  path{
    fill: $ymblue;
  }
}



