@import '../../theme/variables';

/*

Pull Quotes
*/
.pull-quote{
  font-size: 1rem;
  position: relative;
  float: left;
  display: block;
  max-width: 370px;

  blockquote {
    position: relative;
    margin: 0 0 0.875em;
    border: 0;
    padding: 0 2em;

    &:before{
      content: "“";
      font-family: $font-family-sans-serif;
      font-weight: $font_proxima-nova--bold-weight;
      font-size: 5rem;
      line-height: 1.188em;
      height: 5rem;
      position: absolute;
      top: -0.8rem;
      left: -0.3rem;
    }
  }
  p {
    font-family: $font-family-sans-serif;
    font-weight: $font_proxima-nova--regular-weight;
    font-size: 28px;
    line-height: $headings-line-height; // PSD called for 1.188em (32px over 38px line-height), Adjusted to keep better typographic rhythm (1.25em). — JB
    margin: 0;
    & + & {
      margin-top: 1rem;
    }
  }

  cite{
    font-family: $font-family-sans-serif;
    font-size: 1.125rem;
    font-style: italic;
    &:before{
      content: "—";
      display: inline-block;
      padding-right: .25rem;
    }
  }
}



