@import '../../theme/variables';
$gutter-width: 1rem;
.footer_svg-icon--ym-wordmark{
  margin-left: -.2em;
  path{
    fill: #fff;
  }
  @media(min-width: $screen-sm){
    width: 200px;
    height: auto;
  }
  @media(max-width: $screen-sm){
    width: 100%;
    height: auto;
  }
}
.module-footer{
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  margin-top: em(50px);
  color: white;
  .social-link{
    display: inline-block;
    padding-left: .313em;
    padding-right: .313em;
  }
  .svg-icon {
    width: 2.250em;
    fill: white;
  }
  .lead{
    font-size: 1.750em;
    font-weight: 400;
  }
  .primary-row{
    padding: ($grid-gutter-width * 2) 0;
    a{
      color: white;
      text-decoration: none;
    }
  }
  .secondary-row{
    font-size: 0.875em;
    padding: ($grid-gutter-width / 2) 0;
    color: black;
    a{
      font-size: 0.875em;
      color: black;
    }
    [class^="horizontal-list"]{
      margin-left: -.25em;
      margin-right: -.25em;
    }
  }
  .side-column{
    font-size: 1rem;
    display: flex;
  }
  .main-column{
    display: flex;
    flex: 1 0 auto;

    ul{
      @media(min-width: $screen-sm){
        &:not(:last-child){
          padding-right: $grid-gutter-width;
        }
      }
    }
  }
  .link-list__column{
    font-size: 1rem;
    padding: 0;

  }
  .link-list__column + .link-list__column{
    @media(min-width: $screen-sm){
      padding-right: 0;
      padding-left: 3em;
    }
  }
  .link-list{
    display: flex;
    flex-direction: column;
    @media(max-width: $screen-sm){
      margin-bottom: 2em;
    }

    li{
      padding: 0;
      a{
        white-space: nowrap;
        padding: em(2px) 0;
      }
    }
  }
  header{
    font-weight: $font_proxima-nova--semibold-weight;
    margin-bottom: .5em;
    font-size: em(22px);
    white-space: nowrap;
    margin-top: 0.3em;
  }
  .header-text--smallcaps{
    text-transform: uppercase;
    font-weight: $font_proxima-nova--semibold-weight;
    font-size: 0.750em;
    white-space: nowrap;
    margin-bottom: 0.875em;
  }
}

.bg{
  background-color: $ymblue;
}
.container-fluid--no-padding{
  padding: 0;
}

.horizontal-list--vertical-rule{
  display: inline-flex;
  li{
    position: relative;
  }
  li + li{
    @media(min-width: $screen-sm){
      &:before{
        content: '';
        display: inline-block;
        height: 0.625em;
        width: 1px;

        background-color: $border-color;
        position: absolute;
        transform: translateY(56%);
        left: 0;
      }
    }
  }
}



