
@import '../../theme/variables';

.vertical-list{
	font-size: 1rem;
	& > ul{
		flex: 1;
		margin: 0 0 ($grid-gutter-width / 2) 0;
		padding: 0;
		margin-bottom: 0;
		list-style: none;
		flex-direction: column;

		header ~ &{
			padding-top: 0;
		}
	}
	& > ul > li{
		flex: 1;
		padding: ($grid-gutter-width / 4) 0;
		&:first-child{
			padding-top: 0;
		}
		&:last-child{
			padding-bottom: 0;
		}
		& + &{
			border-top: 1px solid #ccc;
			@media (min-width: $screen-sm){
				margin-top: 0;
			}
		}
	}
	& > ul > li > a{
		display: flex;
		white-space: nowrap;

		.fa {
		  display: inline-block;
		  line-height: inherit;

		  &:before, &:after{
		    content: ' ';
		    display: inline;
		  }
		}
	}
	& > ul > li > a:not([class]){
		padding: ($grid-gutter-width / 4) 0;
	}
	li:empty{
		display: none;
	}
}



