/*

 Sass Functions
 */
.horizontal-list {
  display: flex;
  align-items: center;
  font-size: 1rem;
}

.horizontal-list ul {
  display: flex;
  padding: 0;
  margin-bottom: 0;
  list-style: none;
}

@media (min-width: 480px) {
  .horizontal-list ul {
    flex-direction: row;
  }
}

@media (max-width: 480px) {
  .horizontal-list ul {
    flex-direction: column;
  }
}

@media (min-width: 480px) {
  .horizontal-list ul li + .horizontal-list ul li {
    margin-top: 0;
  }
}

.horizontal-list ul li a:not([class]) {
  display: flex;
  white-space: nowrap;
  padding-left: 8px;
  padding-right: 8px;
}

.no-side-padding {
  padding-left: 0;
  padding-right: 0;
}



