@import '../../theme/variables';

.figure-and-caption{
  font-size: 1rem;
  clear: both;
  display: flex;
  flex-direction: column;
  & + & {
    margin-top: 1em;
  }
  &--left{
    float: left;
    margin-right: 1em;
    margin-bottom: .5em;
    text-align: left;
    @media (max-width: $screen-xs){
      margin-left: 0;
      margin-right: 0;
      width: 100%;
      float: none;
    }
    .figure-image {
      @media (min-width: $screen-sm){
        width: em(349px);
      }
      @media (max-width: $screen-sm){
        width: 100%;
      }
    }
  }
  &--right{
    float: right;
    margin-left: 1em;
    margin-bottom: .5em;
    text-align: left;
    @media (max-width: $screen-xs){
      margin-left: 0;
      margin-right: 0;
      width: 100%;
      float: none;
    }
    .figure-image {
      @media (min-width: $screen-sm){
        width: em(349px);
      }
      @media (max-width: $screen-sm){
        width: 100%;
      }
    }
  }
  .figure-title{
    font-size: em(28px);
  }
  .figcaption{
    clear: both;
    display: block;
    margin-top: -.5rem;
  }
  .figure-attribution{
    clear: both;
    float: right;
  }
}
.figure-image{
  clear: both;
  display: block;
  margin-bottom: 1em;
  &--left{
    float: left;
    margin-right: 1em;
    margin-bottom: .5em;
  }
  &--right{
    float: right;
    margin-left: 1em;
    margin-bottom: .5em;
  }
}



