.module-doctorcard {
  font-size: 1rem;
  display: flex;
}

@media (min-width: 768px) {
  .module-doctorcard {
    margin-bottom: 2em;
  }
}

@media (min-width: 768px) {
  .module-doctorcard {
    margin-bottom: 4em;
  }
}

.module-doctorcard > [class*="col-"],
.module-doctorcard > [class*=" col-"] {
  position: relative;
}

.module-doctorcard .panel {
  flex-basis: 100%;
  max-width: 100%;
  display: flex;
  flex: 1;
  flex-direction: row;
  margin-bottom: 0;
  justify-content: flex-start;
  align-items: stretch;
  align-content: space-between;
  background-color: #edf6fb;
}

.module-doctorcard .panel a {
  color: #52A9D9;
}

.module-doctorcard .panel-heading {
  display: flex;
  padding: .6em 0.75em;
  flex: 0 1 auto;
  background-color: #52A9D9;
  color: white;
  font-size: 1.375em;
}

.module-doctorcard .panel-heading > span + span {
  margin-left: .24em;
}

.module-doctorcard .panel-body {
  display: flex;
  padding: 16px;
  flex: 0 1 100%;
  flex-direction: column;
}

.module-doctorcard .panel-body_heading {
  text-transform: uppercase;
  font-size: 0.750em;
  font-weight: bold;
}

.module-doctorcard .panel-body_text--large {
  font-size: 1.625em;
}

.module-doctorcard .panel-footer {
  display: flex;
  padding-left: 1em;
  padding-right: 1em;
  padding-bottom: .5em;
  flex: 0 1 auto;
}

.module-doctorcard .module-doctorcard__figure {
  max-width: 260px;
  position: relative;
}

.module-doctorcard .module-doctorcard__figure img {
  width: 100%;
  height: auto;
}

.module-doctorcard .module-doctorcard__body {
  display: flex;
}

.module-doctorcard .semibold {
  font-weight: 600;
}



