/*

 Sass Functions
 */
.toolbar-container {
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.toolbar-container ul {
  display: flex;
  padding: 0;
  margin-bottom: 0;
  list-style: none;
  font-size: 1rem;
}

@media (min-width: 480px) {
  .toolbar-container ul {
    flex-direction: row;
  }
}

@media (max-width: 480px) {
  .toolbar-container ul {
    flex-direction: column;
  }
}

.toolbar-container ul li {
  font-size: inherit;
}

@media (min-width: 480px) {
  .toolbar-container ul li + .toolbar-container ul li {
    margin-top: 0;
  }
}

.toolbar-container ul li a {
  display: flex;
  white-space: nowrap;
  padding-left: 8px;
  padding-right: 8px;
}

.toolbar-container p {
  margin: 0;
}



