@import '../../theme/variables';

.breadcrumbs-module{
  font-size: 1rem;
  margin-top: -2em;
  margin-bottom: 0.9em;
  position: relative;
  top: -1em;

  &__list{
    font-size: em(13px);
    flex-direction: row;
    flex-wrap: wrap;
    align-items: baseline;
    li {
      font-size: inherit;
      &:after {
        display: inline-block;
        content: '\203A';
        color: #ccc;
        padding-left: 0.45em;
        font-size: em(25px, 13px);
        line-height: 0;
        position: relative;
        top: 0.15385rem;
      }
      &:last-child{
        &:after{
          display: none;
        }
      }
    }

    li + li {
      margin-left: .75em;
    }

  }
  a.breakcrumbs-module__list-link{
    padding: 0;
    hyphens: none;
    white-space: nowrap;
    text-transform: uppercase;
  }
}



