@import '../../theme/variables';
$gutter-width: 1em;
.card {
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  margin-bottom: $grid-gutter-width--rem;
  padding: .5em;
}
.card-deck,
.card-group {
  margin-top: -1px;
  display: flex;
  flex-flow: row wrap;
  & > [class*=" col-"],
  & > [class*="col-"] {
    display: flex;
    flex-direction: row;
    flex: 1 0 100%;
  }
}
.card-deck {
  display: flex;
  flex-flow: row wrap;
  & > [class*=" col-"],
  & > [class*="col-"] {
    display: flex;
    flex-direction: row;
    flex: 1 0 100%;
  }
}
.card-group {
  & > [class*=" col-"],
  & > [class*="col-"] {
    padding-left: 0;
    padding-right: 0;
  }
}

.card--outline {
  background-color: transparent;
  padding: $gutter-width;
  border: 1px solid $border-color;
}
.card--direction-column {
  flex: 1;
}
.card__figure {
  margin: -$gutter-width -$gutter-width 0;
}
.card__figure-overlay {
  position: absolute;
  display: flex;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1em;
  margin: -$gutter-width;
  align-items: flex-start;
  justify-content: flex-end;
}
.card__figure__icon {
  margin-top: 0;
  font-size: 2em;
  vertical-align: text-top;
  line-height: 1.4;
  padding: 0 0.25em;
}
.card__figure + .card-banner {
  margin-top: $gutter-width;
  margin-bottom: $gutter-width;
}
.card__figure + .card-block {
  margin-top: $gutter-width;
}
.card-banner {
  margin: 0 -$gutter-width;
  padding: ($gutter-width / 2) ($gutter-width * 2);
  background-color: #333;
  color: white;
}
.card__figure-overlay__transparent-container {
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  .icon-container {
    background-color: red;
  }
}
.card-block {
  padding: $gutter-width / 2;
  & + & {
    margin-top: -1em;
  }
  &:last-child > *:last-child {
    margin-bottom: 0;
    * + * {
      margin-bottom: 0;
    }
  }
  hr {
    margin-top: $grid-gutter-width;
    margin-bottom: $grid-gutter-width;
  }
}
.card-title {
  font-size: 140%;
  margin-bottom: $gutter-width;
  padding-left: $gutter-width;
  padding-right: $gutter-width;
  .card-block > & {
    padding-left: 0;
    padding-right: 0;
  }
}
.card-subtitle {
  margin-top: -($gutter-width / 2);
  margin-bottom: 0;
}
.card-text:last-child {
  margin-bottom: 0;
}
.card-actions {
  padding: $gutter-width;
  .card-link + .card-link {
    margin-left: $gutter-width;
  }
}
.card-link {
  & + .card-link {
    margin-left: $gutter-width;
  }
}
$themes: ( 'tearose': $tearose, 'ymblue': $ymblue, 'azure': $azure, 'clover': $clover );
@each $name, $color in $themes {
  .card_solid--#{$name} {
    background-color: #{$color};
    color: white;
    &--gradient{
      overflow: hidden;
      position: relative;
      figure {
        z-index: 2;
      }
      &:after{
        content: '';
        height: 2em;

        position: absolute;
        bottom: 0;
        width: 100%;
        height: 2em;
        background: linear-gradient(to bottom, rgba($color, 0) 0%,rgba($color, 1) 100%);
      }
    }
    .card-subtitle,
    .card-title {
      color: white;
    }
    svg {
      fill: white;
    }
    a {
      color: white;
    }
  }
  .card_accented--#{$name} {
    border: 1px solid $border-color;
    &-stroke {
      border-top-color: $color;
    }
    &--gradient{
      overflow: hidden;
      position: relative;
      figure {
        z-index: 2;
      }
      &:after{
        content: '';
        height: 2em;

        position: absolute;
        bottom: 0;
        width: 100%;
        height: 2em;
        background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
      }
    }
    .card-banner {
      background-color: #{$color};
      color: white;
    }
    .card-block hr{
      border-top: 1px solid rgba(white, .45);
    }
    /*
      SVG commented out until specific locations
      are named to avoid style inheritance clashing.
      - JB
    */
    // svg {
    //   fill: #{$color};
    // }
    .card-footer {
      a {
        color: #{$color};
      }
    }
  }
  .card_accented-stroke--#{$name} {
    border: 1px solid $border-color;
    border-top-color: $color;

    &--gradient{
      overflow: hidden;
      position: relative;
      figure {
        z-index: 2;
      }
      &:after{
        content: '';
        height: 2em;

        position: absolute;
        bottom: 0;
        width: 100%;
        height: 2em;
        background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
      }
    }
    .card-banner {
      background-color: #{$color};
      color: white;
    }
    /*
      SVG commented out until specific locations
      are named to avoid style inheritance clashing.
      - JB
    */
    // svg {
    //   fill: #{$color};
    // }
    .card-footer {
      a {
        color: #{$color};
      }
    }
  }
}



