@import '../../theme/variables';
.highlighted-action {
  font-size: 1rem;
  text-align: center;
  & > * {
    margin: 0;
  }
  & > * + * {
    margin-top: 1em;
  }
  .well {
    padding: em(42px) em(70px);
    background-color: yellow;
  }
  h2 {
    font-family: $font-family-sans-serif;
    font-weight: $font_proxima-nova--semibold-weight;
    font-size: em(28px);
  }
  p {
    font-size: em(18px);
  }
}
$themes: ( 'tearose': $tearose, 'ymblue': $ymblue, 'azure': $azure, 'clover': $clover, 'turquoise':$turquoise );
@each $name, $color in $themes {
  .highlightedAction_solid--#{$name} {
		color: white;
    background-color: #{$color} !important;
  }
}



