.search-by-letter {
  font-size: 1rem;
  ul {
    flex-wrap: wrap;
    margin-left: -0.6em;
    margin-right: -0.6em;
  }
  .search-by-letter__link {
    text-transform: uppercase;
    font-size: 1.52em;
    padding-left: 0.57em;
    padding-right: 0.57em;
  }
  a.search-by-letter__all {
    border-left: 1px solid $gray-darker;
    margin-left: 0.4em;
    padding-left: 0.6em;
  }
  .disabled-letter {
    color: grey;
  }
  .selected-letter {
     background-color: $azure;
     text-decoration: none;
     color: white;
  }
}


